import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { TabnavPage } from './tabnav.page';

const routes: Routes = [
  {
    path: '',
    component: TabnavPage,
    children: [
      {
        path: 'timeline',
        loadChildren: () => import('../timeline/timeline.module').then((m) => m.TimelinePageModule),
      },
      {
        path: 'networking',
        loadChildren: () => import('../networking/networking.module').then((m) => m.NetworkingPageModule),
      },
      {
        path: 'opportunities',
        loadChildren: () => import('../opportunities/opportunities.module').then((m) => m.OpportunitiesPageModule),
      },
      {
        path: 'notifications',
        loadChildren: () => import('../notifications/notifications.module').then((m) => m.NotificationsPageModule),
      },
      {
        path: 'academics',
        loadChildren: () => import('../academics/academics.module').then((m) => m.AcademicsPageModule),
      },
      {
        path: '',
        redirectTo: 'timeline',
        pathMatch: 'full',
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TabnavPageRoutingModule {}
