import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { BasePage } from '../base/base.page';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Constants } from '../@global/constants';
import { Store } from '@ngxs/store';
import { UtilsService } from '../@core/utils.service';
import { PushService } from '../@core/push.service';
import { IonTabs } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { INotificationList } from '../@shared/interfaces/notification';
import { NotificationsState } from '../@store/notifications/notifications.state';
import { NavigationExtrasService } from '../@core/navigation-extras.service';
import { IActionIcon } from '../@shared/components/header/header.component';
import { ChatsState } from '../@store/chats/chats.state';
import { Utils } from '../@global/utils';

const MAIL_ICON = 'mail-outline';
@Component({
  selector: 'app-tabnav',
  templateUrl: './tabnav.page.html',
  styleUrls: ['./tabnav.page.scss'],
})
export class TabnavPage extends BasePage implements OnInit, OnDestroy {
  @ViewChild('tabs', { static: false }) tabs: IonTabs;

  public titleImage = Constants.LOGO_PATH;
  public actionIconList: IActionIcon[] = [{ name: 'search' }, { name: MAIL_ICON }];
  private chatListSubscription: Subscription;
  private notificationsSubscription: Subscription;
  public notificationList: INotificationList;
  constructor(
    location: Location,
    router: Router,
    store: Store,
    utilsService: UtilsService,
    private pushService: PushService,
    private navigationExtrasService: NavigationExtrasService
  ) {
    super(location, router, store, utilsService);
    this.pushService.initPush();
  }

  ngOnInit() {
    this.subscribeToNotifications();
    this.subscribeToChatList();
  }

  protected doIonViewWillEnter(): void {
    this.onTabEnter(this.tabs.getSelected());
  }

  public onTabEnter(tab: any): void {
    this.utilsService.publishTabNavEnterEvent(tab.tab || tab);
  }

  public listenIconClick($event) {
    if ($event.index === 0) {
      this.navigationExtrasService.setExtras({ origin: this.router.url });
      this.router.navigate(['/search']);
    } else {
      this.router.navigate(['/messages']);
    }
  }

  private subscribeToNotifications() {
    this.notificationsSubscription = this.store.select(NotificationsState.getUserNotifications).subscribe((data) => {
      this.notificationList = data;
    });
  }

  private subscribeToChatList() {
    this.chatListSubscription = this.store.select(ChatsState.getChatList).subscribe((data) => {
      const iconMailIndex = this.actionIconList.findIndex((icon) => icon.name === MAIL_ICON);
      this.actionIconList[iconMailIndex]['badge'] = Utils.getBadgeValue(data.total_unread_count);
    });
  }

  private disconnectObservers(): void {
    if (this.notificationsSubscription) {
      this.notificationsSubscription.unsubscribe();
    }

    if (this.chatListSubscription) {
      this.chatListSubscription.unsubscribe();
    }
  }

  ngOnDestroy() {
    this.disconnectObservers();
  }
}
